body {
  font-family: 'Open Sans', sans-serif;
  background-color: black;
}

header {
  color: #fff;
  background-color: #11105d;
  padding: 30px 0 20px 10px;
}

header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
  
}

.fa {
  font-size: 22px;
  transform: translateY(50deg);  
}

header i {
  vertical-align: middle;
  margin-left: 12px;
}

.container {
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
}


.wrapper {
  display:flex;
  justify-content: space-between;
  max-width: 1100px;
  margin: 0 auto;
}

.wrapper-1 {
  display:flex;
  flex-direction:column;
  align-items: center;
  max-width: 1100px;
  margin: 0 auto;
}

.add-item {
  border-radius: 12px;
  border-top: 3px solid #11105d;
  padding: 60px 18px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  width: 40%;
}

.display-item {
  width: 75%;
  display:flex;
  flex-wrap: wrap;
  margin-left: 50px;
}

.display-item button {
  margin-top: 20px;
  margin-left: auto;
  display: block;
}

.display-item button:hover {
  background-color: red;
}

.add-item form {
  width: 100%;
  overflow: hidden;
  padding-bottom: 50px;
  border-radius: 12px;
  border-bottom: 3px solid #11105d;
}

input, textarea {
  font-size: 22px;
  color: #000;
  padding: 20px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 0;
  display: block;
  width: 100%;  
  font-family: 'Open Sans', sans-serif;
}

ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

li {
  width: calc(50% - 50px);
  border-radius: 12px;
  list-style-type: none;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  color: #000;
  margin-bottom: 30px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
}

h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

li h3 {
  background-color:#11105d;
  border-radius: 9px;
  margin: 0;
  color: #fff;
  font-weight: 300;
  padding: 15px;
}

li p {
  padding: 7px;
}

form button {
  width: 100%;
  margin-top: 10px;
}

button {
  border-radius: 12px;
  min-width: 88px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 14px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

.user-profile {
  position: fixed;
  bottom: 20px;
  right: 40px;
  width: 50px;
  height: 50px;
}

.user-profile img {
  border-radius: 50%;
  max-width: 100%;
}

.text-text {
  margin-top: 20px;
  margin-bottom: 20px;
  color:white
}